import React from 'react';
import NewsCard from './NewsCard'
import './News.scss'

import { useQuery, gql } from '@apollo/client';

const GET_ARTICLES = gql`query {articles {
    edges {
      node {
        id, pubDateTime, title, sourceName, 
        articleUrl,  articleSummary, imageUrl
      }
      cursor
    } 
    pageInfo {startCursor, endCursor, hasNextPage}
    totalCount
  }}`;

  function toArticles(graphQlRes) {
    return graphQlRes?.articles?.edges?.map( x=> x.node) || [];
    }

function News() {

    const { loading, error, data } = useQuery(GET_ARTICLES);

    return (
        <div className="news-flex">
            {loading ? (<h2>Loading ...</h2>) : error ?(<h2>Failed to load ...</h2>) : toArticles(data).map((article) =>
                (
                    <NewsCard
                        key={article.id}
                        title={article.title}
                        description={article.articleSummary}
                        link={article.articleUrl}
                        sourceName={article.sourceName}
                        dateTime={article.pubDateTime}
                        imgLink={article.imageUrl}
                    />
                )
            )}
        </div>
    )
}

export default News

